.App{
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  margin: 10px;
}

.graph{
  display: inline-block;
  margin: 2px;
}
.graph-tick{
  display: flex;
  justify-content: space-between;
  margin-left: 2px;
  margin-right: 2px;

}
.graph-tick>span{
  font-size: 10px;
  margin-bottom: 0;
  color:#868585;
}


.title{
  display: flex;
  justify-content: space-between;
  align-items: baseline;
}

.cpu-name{
  font-family: 'Segoe UI semibold', Tahoma, Geneva, Verdana, sans-serif;
  font-size: 14px;
  width: 50%;
  border:0px;
}
input{
  padding: 0px;
  border:0px;
  font-weight: 500;
  font-size: 10px;
}

.cpu-detail{
  min-width: 570px;
}

.parts-list{
  min-width: 250px;
  border-right: solid 1px #f3f3f3;
}

.parts-card{
  display: flex;
  align-items: end;
  padding: 10px;
}
.information{
  font-size: 0;
}

